.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0 0 0 0);
}

.customer-reviews__thumb {
  width: 100%;
  text-align: center;

  padding: 0 5px;

  /*@media screen and (min-width: 768px) {
    padding: 0 10px;
  }

  @media screen and (min-width: 1280px) {
    padding: 0 20px;
  }*/
}

.customer-reviews__picture {
  display: inline-block;
  width: 85px;
  height: 85px;
  border-radius: 50%;
  object-fit: contain;
  margin-bottom: 70px;
  overflow: hidden;
}

.customer-reviews__content-container {
  padding: 30px 50px;
  background-color: #fafafa;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 4px;
  margin-bottom: 15px;
}

.customer-reviews__content {
  position: relative;
  padding: 3px 10px;
  z-index: 2;

  background-image: repeating-linear-gradient(
    to bottom,
    transparent,
    transparent 28px,
    #e1e1e1 31px
  );

  @include font(500, 12px, 31px, 0.04em, none);
  color: var(--minor-text-color);

  @media screen and (min-width: 768px) {
    padding: 3px 20px;
    @include font(500, 14px, 30px, 0.04em, none);
  }

  @media screen and (min-width: 1280px) {
    padding: 3px 30px;
    @include font(500, 16px, 30px, 0.04em, none);
  }

  &::before {
    position: absolute;
    top: -15px;
    left: 4px;
    z-index: -1;
    width: 54px;
    height: 40px;

    content: '';
    background-image: url('../images/quotes.svg');
    background-repeat: no-repeat;

    @media screen and (min-width: 768px) {
      top: -15px;
      left: -20px;
    }

    @media screen and (min-width: 1280px) {
      top: -15px;
      left: 0px;
    }
  }
}

.customer-reviews__data {
  position: relative;
  width: 100%;
  min-height: 90px;
  padding-top: 22px;

  &::before {
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    content: '';
    width: 30px;
    height: 4px;
    background-image: url('../images/3dots.svg');
    background-repeat: no-repeat;
    background-size: contain;
    background-position: 50% 50%;
  }
}

.customer-reviews__name {
  @include font(500, 14px, 26px, 0.04em, none);
  color: var(--minor-text-color);

  @media screen and (min-width: 768px) {
    @include font(500, 16px, 30px, 0.04em, none);
  }

  @media screen and (min-width: 1280px) {
    @include font(500, 20px, 38px, 0.04em, none);
  }
}

.customer-reviews__container {
  position: relative;
}

.arrow-prev {
  position: absolute;
  top: 20px;
  left: 20px;
  cursor: pointer;

  fill: var(--accent-first-color);

  transition: fill 250ms cubic-bezier(0.4, 0, 0.2, 1);

  @media screen and (min-width: 768px) {
    left: 80px;
  }

  @media screen and (min-width: 1280px) {
    left: 120px;
  }
  &:hover,
  &:focus {
    fill: var(--accent-second-color);
  }
}

.arrow-next {
  position: absolute;
  top: 20px;
  right: 20px;

  cursor: pointer;

  fill: var(--accent-first-color);

  transition: fill 250ms cubic-bezier(0.4, 0, 0.2, 1);

  @media screen and (min-width: 768px) {
    right: 80px;
  }

  @media screen and (min-width: 1280px) {
    right: 120px;
  }

  &:hover,
  &:focus {
    fill: var(--accent-second-color);
  }
}

.slick-dots {
  position: absolute;
  bottom: -30px;
}

.slick-dots li {
  width: 12px;
  height: 12px;
  background-image: url('../images/ellipse-gray.svg');
  background-repeat: no-repeat;
  background-size: contain;
  cursor: pointer;

  &:not(:last-child) {
    margin-right: 5px;
    @media screen and (min-width: 768px) {
      margin-right: 12px;
    }
  }

  & button {
    display: none;
  }

  transition: background-image 250ms cubic-bezier(0.4, 0, 0.2, 1);

  &.slick-active {
    width: 12px;
    height: 12px;
    background-image: url('../images/home.svg');
    background-repeat: no-repeat;
    background-size: contain;
  }

  &.slick-active:hover,
  &.slick-active:focus {
    width: 12px;
    height: 12px;
    background-image: url('../images/home.svg');
    background-repeat: no-repeat;
    background-size: contain;
  }

  &:hover,
  &:focus {
    background-image: url('../images/ellipse-secondary-color.svg');
  }
}
