.section__gallery {
  width: 100%;
  height: auto;

  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  background-color: var(--main-background-color);
  background-image: linear-gradient(
    to top,
    var(--third-color) 45%,
    var(--third-color) 55%,
    var(--main-background-color) 55%
  );
